<template>
  <div>
    <div v-for="notification in notifications" :key="notification.id" class="mb-3">
      <invited-to-event :notifData="notification.data"  v-if="notification.data.type == 'event.guest.invite.notification'" />
      <removed-from-event :notifData="notification.data" v-else-if="notification.data.type == 'event.guest.remove.notification'" />
      <user-followed :notifData="notification.data" v-else-if="notification.data.type == 'user.follow.notification'" />
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex';
import InvitedToEvent from '@/components/notification/items/InvitedToEvent';
import RemovedFromEvent from '@/components/notification/items/RemovedFromEvent';
import UserFollowed from '@/components/notification/items/UserFollowed';

export default {
  components: {
    InvitedToEvent,
    RemovedFromEvent,
    UserFollowed
  },
  computed: {
    ...mapState('notification', [
      'notifications'
    ])
  }
}
</script>