<template>
  <div>
    <div>
      You have invited to {{ notifData.event_title }}
      <br>
      Would you accept this invitation?
      <div>
        <button @click="resp(notifData.event_id, 1)">Yes</button> | 
        <button @click="resp(notifData.event_id, 2)">No</button>
      </div>
    </div>
  </div>
</template>

<script>
import EventService from '@/services/event.service';

export default {
  props: {
    notifData: {
      type: Object
    }
  },
  methods: {
    resp(eventId, status) {
      // status : 1.Accept , 2.Reject
      EventService.invititionGuestResponse(eventId, status)
        .then(res=> console.log(res))
        .catch(err=> console.log(err.response));
    }
  },
}
</script>