<template>
  <div>
    <div>
      {{ notifData.follower_username }} wants to follow you. Would you accept?
      <br>
      <button @click="resp(1)">Yes</button> , 
      <button @click="resp(0)">No</button> 
      <br>
    </div>
  </div>
</template>

<script>
import profileService from '@/services/profile.service'

export default {
  props: {
    notifData: {
      type: Object
    }
  },
  methods: {
    resp(respond) {
      profileService.followRespond(this.notifData.follower_id, respond)
        .then(res=> console.log(res, 'ok'))
        .catch(err=> console.log(err, 'error'));
    }
  },
}
</script>